export const MobilityAidAccessibility = {
    name: "Mobility Aid Accessibility",
    displayName: "Mobility Aid Accessibility Requirements",
    param: "mobility-aid-accessibility",
    subTitle: "Mobility Aid Accessibility",
    paragraphs: [
        {text: `(a)(1) General. All intercity rail cars, other than level entry cars, required to be accessible by §§38.111 (a) and (e) of this subpart shall provide a level-change mechanism or boarding device (e.g., lift, ramp or bridge plate) complying with either paragraph (b) or (c) of this section and sufficient clearances to permit a wheelchair or other mobility aid user to reach a seating location complying with paragraph (d) of this section.`},
        {text: `(2) Exception. If level-entry boarding, portable or platform lifts, ramps or bridge plates meeting the applicable requirements of this section are provided on station platforms or other stops required to be accessible, or mini-high platforms complying with §38.113(d) are provided, the car is not required to be equipped with a car-borne device. The access systems or devices used at a station to which section 37.42 applies must permit compliance with that section.`},        
        {text: `(b) Car Lift—(1) Design load. The design load of the lift shall be at least 600 pounds. Working parts, such as cables, pulleys, and shafts, which can be expected to wear, and upon which the lift depends for support of the load, shall have a safety factor of at least six, based on the ultimate strength of the material. Nonworking parts, such as platform, frame, and attachment hardware which would not be expected to wear, shall have a safety factor of at least three, based on the ultimate strength of the material.`},
        {text: `(2) Controls—(i) Requirements. The controls shall be interlocked with the car brakes, propulsion system, or door, or shall provide other appropriate mechanisms or systems, to ensure that the car cannot be moved when the lift is not stowed and so the lift cannot be deployed unless the interlocks or systems are engaged. The lift shall deploy to all platform levels normally encountered in the operating environment. Where provided, each control for deploying, lowering, raising, and stowing the lift and lowering the roll-off barrier shall be of a monetary contact type requiring continuous manual pressure by the operator and shall not allow improper lift sequencing when the lift platform is occupied. The controls shall allow reversal of the lift operation sequence, such as raising or lowering a platform that is part way down, without allowing an occupied platform to fold or retract into the stowed position.`},        
        {text: `(ii) Exception. Where physical or safety constraints prevent the deployment at some stops of a lift having its long dimension perpendicular to the car axis, the transportation entity may specify a lift which is designed to deploy with its long dimension parallel to the car axis and which pivots into or out of the car while occupied (i.e., “rotary lift”). The requirements of paragraph (b)(2)(i) of this section prohibiting the lift from being stowed while occupied shall not apply to a lift design of this type if the stowed position is within the passenger compartment and the lift is intended to be stowed while occupied.`},        
        {text: `(iii) Exception. The brake or propulsion system interlocks requirement does not apply to platform mounted or portable lifts provided that a mechanical, electrical or other system operates to ensure that cars do not move when the lift is in use.`},        
        {text: `(3) Emergency operation. The lift shall incorporate an emergency method of deploying, lowering to ground or station platform level with a lift occupant, and raising and stowing the empty lift if the power to the lift fails. No emergency method, manual or otherwise, shall be capable of being operated in a manner that could be hazardous to the lift occupant or to the operator when operated according to manufacturer's instructions, and shall not permit the platform to be stowed or folded when occupied, unless the lift is a rotary lift and is intended to be stowed while occupied.`},
        {text: `(4) Power or equipment failure. Platforms stowed in a vertical position, and deployed platforms when occupied, shall have provisions to prevent their deploying, falling, or folding any faster than 12 inches/second or their dropping of an occupant in the event of a single failure of any load carrying component.`},
        {text: `(5) Platform barriers. The lift platform shall be equipped with barriers to prevent any of the wheels of a wheelchair or mobility aid from rolling off the lift during its operation. A movable barrier or inherent design feature shall prevent a wheelchair or mobility aid from rolling off the edge closest to the car until the lift is in its fully raised position. Each side of the lift platform which, in its raised position, extends beyond the car shall have a barrier a minimum 11⁄2 inches high. Such barriers shall not interfere with maneuvering into or out of the car. The loading-edge barrier (outer barrier) which functions as a loading ramp when the lift is at ground or station platform level, shall be sufficient when raised or closed, or a supplementary system shall be provided, to prevent a power wheelchair or mobility aid from riding over or defeating it. The outer barrier of the lift shall automatically rise or close, or a supplementary system shall automatically engage, and remain raised, closed, or engaged at all times that the lift platform is more than 3 inches above the station platform and the lift is occupied. Alternatively, a barrier or system may be raised, lowered, opened, closed, engaged or disengaged by the lift operator provided an interlock or inherent design feature prevents the lift from rising unless the barrier is raised or closed or the supplementary system is engaged.`},
        {text: `(6) Platform surface. The lift platform surface shall be free of any protrusions over 1⁄4 inch high and shall be slip resistant. The lift platform shall have a minimum clear width of 281⁄2 inches at the platform, a minimum clear width of 30 inches measured from 2 inches above the lift platform surface to 30 inches above the surface, and a minimum clear length of 48 inches measured from 2 inches above the surface of the platform to 30 inches above the surface. (See Fig. 1.)`},
        {text: `(7) Platform gaps. Any openings between the lift platform surface and the raised barriers shall not exceed 5⁄8 inch wide. When the lift is at car floor height with the inner barrier (if applicable) down or retracted, gaps between the forward lift platform edge and car floor shall not exceed 1⁄2 inch horizontally and 5⁄8 inch vertically.`},
        {text: `(8) Platform entrance ramp. The entrance ramp, or loading-edge barrier used as a ramp, shall not exceed a slope of 1:8, when measured on level ground, for a maximum rise of 3 inches, and the transition from station platform to ramp may be vertical without edge treatment up to 1⁄4 inch. Thresholds between 1⁄4 inch and 1⁄2 inch high shall be beveled with a slope no greater than 1:2.`},
        {text: `(9) Platform deflection. The lift platform (not including the entrance ramp) shall not deflect more than 3 degrees (exclusive of car roll) in any direction between its unloaded position and its position when loaded with 600 pounds applied through a 26 inch by 26 inch test pallet at the centroid of the lift platform.`},
        {text: `(10) Platform movement. No part of the platform shall move at a rate exceeding 6 inches/second during lowering and lifting an occupant, and shall not exceed 12 inches/second during deploying or stowing. This requirement does not apply to the deployment or stowage cycles of lifts that are manually deployed or stowed. The maximum platform horizontal and vertical acceleration when occupied shall be 0.3g.`},
        {text: `(11) Boarding direction. The lift shall permit both inboard and outboard facing of wheelchairs and mobility aids.`},
        {text: `(12) Use by standees. Lifts shall accommodate persons using walkers, crutches, canes or braces or who otherwise have difficulty using steps. The lift may be marked to indicate a preferred standing position.`},
        {text: `(13) Handrails. Platforms on lifts shall be equipped with handrails, on two sides, which move in tandem with the lift, and which shall be graspable and provide support to standees throughout the entire lift operation. Handrails shall have a usable component at least 8 inches long with the lowest portion a minimum 30 inches above the platform and the highest portion a maximum 38 inches above the platform. The handrails shall be capable of withstanding a force of 100 pounds concentrated at any point on the handrail without permanent deformation of the rail or its supporting structure. The handrail shall have a cross-sectional diameter between 11⁄4 inches and 11⁄2 inches or shall provide an equivalent grasping surface, and have eased edges with corner radii of not less than 1⁄8 inch. Handrails shall be placed to provide a minimum 11⁄2 inches knuckle clearance from the nearest adjacent surface. Handrails shall not interfere with wheelchair or mobility aid maneuverability when entering or leaving the car.`},
        {text: `(c) Car ramp or bridge plate—(1) Design load. Ramps or bridge plates 30 inches or longer shall support a load of 600 pounds, placed at the centroid of the ramp or bridge plate distributed over an area of 26 inches by 26 inches, with a safety factor of at least 3 based on the ultimate strength of the material. Ramps or bridge plates shorter than 30 inches shall support a load of 300 pounds.`},
        {text: `(2) Ramp surface. The ramp or bridge plate surface shall be continuous and slip resistant, shall not have protrusions from the surface greater than 1⁄4 inch high, shall have a clear width of 30 inches and shall accommodate both four-wheel and three-wheel mobility aids.`},
        {text: `(3) Ramp threshold. The transition from station platform to the ramp or bridge plate and the transition from car floor to the ramp or bridge plate may be vertical without edge treatment up to 1⁄4 inch. Changes in level between 1⁄4 inch and 1⁄2 inch shall be beveled with a slope no greater than 1:2.`},
        {text: `(4) Ramp barriers. Each side of the ramp or bridge plate shall have barriers at least 2 inches high to prevent mobility aid wheels from slipping off.`},
        {text: `(5) Slope. Ramps or bridge plates shall have the least slope practicable. If the height of the vehicle floor, under 50% passenger load, from which the ramp is deployed is 3 inches or less above the station platform a maximum slope of 1:4 is permitted; if the height of the vehicle floor, under 50% passenger load, from which the ramp is deployed is 6 inches or less, but more than 3 inches, above the station platform a maximum slope of 1:6 is permitted; if the height of the vehicle floor, under 50% passenger load, from which the ramp is deployed is 9 inches or less, but more than 6 inches, above the station platform a maximum slope of 1:8 is permitted; if the height of the vehicle floor, under 50% passenger load, from which the ramp is deployed is greater than 9 inches above the station platform a slope of 1:12 shall be achieved. Folding or telescoping ramps are permitted provided they meet all structural requirements of this section.`},
        {text: `(6) Attachment—(i) Requirement. When in use for boarding or alighting, the ramp or bridge plate shall be attached to the vehicle, or otherwise prevented from moving such that it is not subject to displacement when loading or unloading a heavy power mobility aid and that any gaps between vehicle and ramp or bridge plate, and station platform and ramp or bridge plate, shall not exceed 5⁄8 inch.`},
        {text: `(ii) Exception. Ramps or bridge plates which are attached to, and deployed from, station platforms are permitted in lieu of car devices provided they meet the displacement requirements of paragraph (c)(6)(i) of this section.`},
        {text: `(7) Stowage. A compartment, securement system, or other appropriate method shall be provided to ensure that stowed ramps or bridge plates, including portable ramps or bridge plates stowed in the passenger area, do not impinge on a passenger's wheelchair or mobility aid or pose any hazard to passengers in the event of a sudden stop.`},
        {text: `(8) Handrails. If provided, handrails shall allow persons with disabilities to grasp them from outside the car while starting to board, and to continue to use them throughout the boarding process, and shall have the top between 30 inches and 38 inches above the ramp surface. The handrails shall be capable of withstanding a force of 100 pounds concentrated at any point on the handrail without permanent deformation of the rail or its supporting structure. The handrail shall have a cross-sectional diameter between 11⁄4 inches and 11⁄2 inches or shall provide an equivalent grasping surface, and have eased edges with corner radii of not less than 1⁄8 inch. Handrails shall not interfere with wheelchair or mobility aid maneuverability when entering or leaving the car.`},
        {text: `(d) Seating—(1) Requirements. All intercity rail cars required to be accessible by §§38.111 (a) and (e) of this subpart shall provide at least one, but not more than two, mobility aid seating location(s) complying with paragraph (d)(2) of this section; and at least one, but not more than two, seating location(s) complying with paragraph (d)(3) of this section which adjoin or overlap an accessible route with a minimum clear width of 32 inches.`},
        {text: `(2) Wheelchair or mobility aid spaces. Spaces for persons who wish to remain in their wheelchairs or mobility aids shall have a minimum clear floor space 48 inches by 30 inches. Such spaces shall adjoin, and may overlap, an accessible path. Not more than 6 inches of the required clear floor space may be accommodated for footrests under another seat provided there is a minimum of 9 inches from the floor to the lowest part of the seat overhanging the space. Seating spaces may have fold-down or removable seats to accommodate other passengers when a wheelchair or mobility aid user is not occupying the area, provided the seats, when folded up, do not obstruct the clear floor space provided (See Fig. 2).`},
        {text: `(3) Other spaces. Spaces for individuals who wish to transfer shall include a regular coach seat or dining car booth or table seat and space to fold and store the passenger's wheelchair.`},
    ]
}