export const ADA =  {
    name: "ADA",
    displayName: "ADA Requirements",
    param: "ada",
    subTitle: "ss 38.111 General.",
    paragraphs: [
        {text: `(a) New, used and remanufactured intercity rail cars, to be considered accessible by regulations in part<link="id_37"><color=blue> 37 </color></link>of this title shall comply with this subpart to the extent required for each type of car as specified below.`},
        {text: `(1) Single-level rail passenger coaches and food service cars (other than single-level dining cars) shall comply with <link=id_38113><color=blue>§§38.113 through 38.123 </color></link> of this part. Compliance with<link=id_38125><color=blue> §38.125</link></color> of this part shall be required only to the extent necessary to meet the requirements of paragraph (d) of this section.`},
        {text: `(2) Single-level dining and lounge cars shall have at least one connecting doorway complying with<link=id_38113><color=blue> §38.113(a)(2)</color></link> of this part connected to a car accessible to persons using wheelchairs or mobility aids, and at least one space complying with<link=id_38125><color=blue> §§38.125(d) (2) and (3) </color></link> of this part, to provide table service to a person who wishes to remain in his or her wheelchair, and space to fold and store a wheelchair for a person who wishes to transfer to an existing seat.`},
        {text: `(3) Bi-level dining cars shall comply with <link=id_38113><color=blue> §§38.113(a)(2)</color></link>, <link=id_38115><color=blue> 38.115(b)</link></color>, <link=id_38117><color=blue> 38.117(a)</color></link>, and <link=id_38121><color=blue> 38.121</color></link> of this part.`},
        {text: `(4) Bi-level lounge cars shall have doors on the lower level, on each side of the car from which passengers board, complying with <link=id_38113><color=blue>§38.113</color></link>, a restroom complying with <link=id_38123><color=blue>§38.123</color></link>, and at least one space complying with <link=id_38125><color=blue>§38.125(d) (2) and (3)</color></link> to provide table service to a person who wishes to remain in his or her wheelchair and space to fold and store a wheelchair for a person who wishes to transfer to an existing seat.`},
        {text: `(5) Restrooms, complying with <link=id_38123><color=blue>§38.123</color></link> shall be provided in single-level rail passenger coaches and food services cars adjacent to the accessible seating locations required by paragraph (d) of this section. Accessible restrooms are required in dining and lounge cars only if restrooms are provided for other passengers.`},
        {text: `(6) Sleeper cars shall comply with <link=id_38113><color=blue>§§38.113 (b) through (d)</color></link>, <link=id_38115><color=blue>38.115 through 38.121</color></link>, and <link=id_38125><color=blue>38.125</color></link>, of this part, and have at least one compartment which can be entered and used by a person using a wheelchair or mobility aid and complying with <link=id_38127><color=blue>§38.127</color></link> of this part.`},
        {text: `(b)`},
        {text: `(1) Unless structurally or operationally impracticable, intercity rail cars shall comply with <link=id_38113><color=blue>§38.113(d)</color></link> of this part for level boarding.`},
        {text: `(2) Where level boarding is structurally or operationally impracticable, intercity rail cars shall comply with <link=id_38125><color=blue>§38.125</color></link>.`},
        {text: `(c) If portions of the car are modified in a way that it affects or could affect accessibility, each such portion shall comply, to the extent practicable, with the applicable provisions of this subpart. This provision does not require that inaccessible cars be retrofitted with lifts, ramps or other boarding devices.`},
        {text: `(d) Passenger coaches or food service cars shall have the number of spaces complying with <link=id_38125><color=blue>§38.125(d)(2)</color></link> of this part and the number of spaces complying with <link=id_38125><color=blue>§38.125(d)(3)</color></link> of this part, as required by <link=id_3791><color=blue>§37.91</color></link> of this title.`},
        {text: `(e) Existing cars retrofitted to meet the seating requirements of <link=id_3791><color=blue>§37.91</color></link> of this title shall comply with <link=id_38113><color=blue>§38.113(e)</color></link>, <link=id_38123><color=blue>§38.123</color></link>, <link=id_38125><color=blue>§38.125(d)</color></link> of this part and shall have at least one door on each side from which passengers board complying with <link=id_38113><color=blue>§38.113(d)</color></link> of this part. Existing cars designed and manufactured to be accessible in accordance with the Secretary of Transportation regulations implementing section 504 of the Rehabilitation Act of 1973 that were in effect before October 7, 1991, shall comply with <link=id_38125><color=blue>§38.125(a)</color></link> of this part.`},
    ]
}